export const bondsSlice = createSlice({
  name: "bond",
  initialState,
  reducers: {
    setBondHeader: (state, action) => {
      return produce(state, (draft) => {
        //    produce
        //          `isin`      `bonds`
        const bondIndex = draft.bonds.findIndex(
          (bond) => bond.bondHeader.isin === action.payload.isin
        );
        if (bondIndex !== -1) {
          //    `bondHeader`      
          draft.bonds[bondIndex].bondHeader = action.payload;
        }
      });
    },
    setBondPay: (state, action) => {
      return produce(state, (draft) => {
        draft.bondTables = action.payload.map((payment) => ({
          body: [
            [
              payment.type,
              payment.date.split("-").reverse().join("."), //     
              payment.amount,
              payment.percent + "%",
              payment.remaining,
            ],
          ],
          mobile: {
            body: [
              [
                [
                  payment.date.split("-").reverse().join("."), //     
                  payment.type,
                ],
                payment.amount,
                payment.percent + "%",
              ],
            ],
          },
        }));
      });
    },
    // setBondActions: (state, action) => {
    //   return produce(state, (draft) => {
    //     draft.bondTables[1] = action.payload.map((actionItem) => ({
    //       body: [
    //         [
    //           actionItem.date.split("-").reverse().join("."), //     
    //           actionItem.type,
    //           actionItem.start.split("-").reverse().join("."),
    //           actionItem.end.split("-").reverse().join("."),
    //         ],
    //       ],
    //       //    mobile
    //     }));
    //   });
    // },
  },
});

// Action creators are generated for each case reducer function
export const { setBondHeader, setBondPay, setBondActions } = bondsSlice.actions;

export default bondsSlice.reducer;
